.popup__container {
  min-width: 430px;
  max-height: 330px;
  background-color: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
}

@media (max-width: 603px) {
  .popup__container {
    max-width: 282px;
    min-height: 322px;
  }
}

